.wp-block-dining-dashboard {
    &-menu-section {

        &.align {

            &-narrow {
                max-width: 60%;
            }

            &-wide {
                max-width: 80%;
            }

            &-full {
                max-width: 100%;
            }

        }

        &-grid {
            display: grid;

            &.cols-1 {
                grid-template-columns: 1fr;
            }

            &.cols-2 {
                grid-template-columns: 1fr 1fr;
            }

            &.cols-3 {
                grid-template-columns: 1fr 1fr 1fr;
            }

            &.cols-4 {
                grid-template-columns: 1fr 1fr 1fr 1fr;
            }

        }

    }
}